@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap";
.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  background: url("bg.544cebea.webp") 0 0 / cover;
  justify-content: center;
  align-items: center;
  font: 18px Montserrat, sans-serif;
  display: flex;
}

*, :before, :after {
  box-sizing: inherit;
}

:root {
  --tittle-color: #212121;
  --description-color: #757575;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.app-card {
  width: 400px;
  text-align: center;
  background-color: rgba(255, 255, 255, .5);
  border-radius: 10px;
  flex-direction: column;
  gap: 1.5rem;
  display: flex;
  top: 50%;
  bottom: 50%;
  overflow: hidden;
  box-shadow: 0 15px 50px -20px #000;
}

button[type="submit"] {
  cursor: pointer;
  background: rgba(255, 255, 255, .3);
  border: none;
  border-left: 1px solid #000;
  border-radius: 8px;
  margin-left: -15px;
  padding: 5px;
}

input[type="text"] {
  text-align: center;
  background: rgba(255, 255, 255, .2);
  border: none;
  border-radius: 8px 0 0 8px;
  padding: 5px;
  font-weight: 600;
}

input[type="text"]:hover, input[type="text"]:focus {
  background: #fff;
  outline: 0;
}

.name-info {
  font-size: 50px;
  font-weight: 700;
}

.info {
  margin-right: 5px;
}

.weather-info {
  color: #000;
  font-size: 40px;
  font-weight: 700;
}

